<template>
  <div class="iv-container">
    <!--个车流程步骤条-->
    <div class="step">
      <recommend-step :step="stepProcess" :steps="steps"></recommend-step>
    </div>
    <div class="c-main">
      <!--查询条件-->
      <div class="form">
        <query-form @recommend="recommend" @querySector="querySector"></query-form>
      </div>
      <!--业务组件渲染-->
      <router-view/>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const QueryForm = () => import('./QueryForm')
const RecommendStep = () => import('@/components/RecommendStep')

export default {
  name: 'IndividualVehicle',
  components: {RecommendStep, QueryForm},
  data () {
    return {
      steps: [
        {id: '1', title: '推荐参数配置', icon: 'iconfont icon-ziyuan1', path: '/admin/individual/declare/paramConfig'},
        {id: '2', title: '智能推荐', icon: 'iconfont icon-ziyuan2', path: '/admin/individual/declare/recommend'},
        {id: '3', title: '自定义维度切分', icon: 'iconfont icon-ziyuan3', path: '/admin/individual/declare/custom'},
        {id: '4', title: '确认业务小类结构', icon: 'iconfont icon-ziyuan4', path: '/admin/individual/declare/confirm'},
        {id: '5', title: '编制报价策略', icon: 'iconfont icon-ziyuan5', path: '/admin/individual/declare/config'},
        {id: '6', title: '补充报价规则', icon: 'iconfont icon-ziyuan6', path: '/admin/individual/declare/supply'}
      ]
    }
  },
  computed: mapState({
    stepProcess: state => state.individual.stepProcess,
    lifeFormOptionShow: state => state.individual.lifeFormOptionShow,
    lifeFormReadOnly: state => state.individual.lifeFormReadOnly,
    versionFlag: state => state.individual.versionFlag
  }),
  methods: {
    querySector () {
      this.$store.dispatch('queryParam').then(() => {
        this.$router.push({path: '/admin/individual/declare/paramConfig'})
      })
    },
    recommend () {
      this.$router.push({path: '/admin/individual/declare/recommend'})
    }
  }
}
</script>

<style scoped>
  .iv-container > .step {
    padding: 20px 0;
    height: 58px;
  }

  .iv-container > .form {
    padding-top: 16px;
    padding-left: 20px;
    background: #fff;
  }

  .sector-table {
    border-top: solid 1px #eee;
    border-right: solid 1px #eee;
    border-left: solid 1px #eee;
  }
  .c-main{
    border:1px solid #E4E4E4;
    border-radius: 9px;
    background:#fff;
  }
  .c-main > .form{
    background: linear-gradient(to bottom, #f9f9f9, white);
    padding:15px 20px;
    border-bottom: 1px solid #e4e4e4;
    border-radius: 9px 9px 0 0 ;
  }
  .el-form-item--small.el-form-item{
    margin-bottom: 0;
  }
</style>



